import React, { useCallback, useState } from 'react'
import styled from 'styled-components'
import { Button, Text } from 'uikit'
import { AlertTriangle } from 'react-feather'
import { useTranslation } from 'contexts/Localization'
import Modal from '../Modal'
import { AutoRow, RowBetween } from '../Row'
import { AutoColumn } from '../Column'

const WarningContainer = styled.div`
  max-width: 420px;
  width: 100%;
  padding: 1rem;
  background: rgba(242, 150, 2, 0.05);
  border: 1px solid #f3841e;
  border-radius: 20px;
  overflow: auto;
`

const StyledWarningIcon = styled(AlertTriangle)`
  stroke: ${({ theme }) => theme.colors.binance};
`

export default function SafeMoonWarningModal({ isOpen, token, onConfirm }: { isOpen: boolean; token: any, onConfirm: () => void }) {
  const [understandChecked, setUnderstandChecked] = useState(false)

  const toggleUnderstand = useCallback(() => setUnderstandChecked((uc) => !uc), [])

  const handleDismiss = useCallback(() => null, [])

  const { t } = useTranslation()

  return (
    <Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
      <WarningContainer className="token-warning-container">
        <AutoColumn gap="lg">
          <AutoRow gap="6px">
            <StyledWarningIcon />
            <Text>{t('trandnotice', { name : token?.symbol ? token?.symbol : '' })}</Text>
          </AutoRow>
          <>
            <Text>
              {t('tnsetting',  { name : token?.symbol ? token?.symbol : '' })} 
              <strong>{t('setST', {value : (token?.tokenInfo?.extensions?.fee + 2).toString()})}%+</strong>
            </Text>
            <Text>{t('setReason', {name: token?.symbol ? token?.symbol : '', value: (token?.tokenInfo?.extensions?.fee + 0).toString()})}</Text>
            {token?.tokenInfo?.extensions?.share_fee && <Text> {t('setShareFee', {value: (token?.tokenInfo?.extensions?.share_fee + 0).toString()})}</Text>}
            {token?.tokenInfo?.extensions?.lp_fee && <Text> {t('setlpFee', {value: (token?.tokenInfo?.extensions?.lp_fee + 0).toString()})}</Text>}
            {token?.tokenInfo?.extensions?.burn_fee && <Text> {t('setBurnFee', {value: (token?.tokenInfo?.extensions?.burn_fee + 0).toString()})}</Text>}
            {token?.tokenInfo?.extensions?.bonus_fee && <Text> {t('setBonusFee', {value: (token?.tokenInfo?.extensions?.bonus_fee + 0).toString()})}</Text>}
          </>
          <RowBetween>
            <div>
              <label htmlFor="understand-checkbox" style={{ cursor: 'pointer', userSelect: 'none' }}>
                <input
                  id="understand-safeMoonWarning"
                  type="checkbox"
                  className="understand-checkbox"
                  checked={understandChecked}
                  onChange={toggleUnderstand}
                />{' '}
                <Text as="span">{t("I understand")}</Text>
              </label>
            </div>
            <Button
              id="confirm-safeMoonWarning"
              disabled={!understandChecked}
              variant="danger"
              style={{ width: '140px' }}
              onClick={() => {
                setUnderstandChecked(false)
                onConfirm()
              }}
            >
              {t("Continue")}
            </Button>
          </RowBetween>
        </AutoColumn>
      </WarningContainer>
    </Modal>
  )
}
